import React, { Component } from 'react';
import { getToken, fetch, fetch_by_id } from './utils';
import { URL } from './constants';

class App extends Component {
  state = {
    is_all: false,
    is_platform: false,
    routes: [],
    directions: [],
    platforms: [],
    selected: {
      route: null,
      direction: null,
      platform: null
    },
    audio: {
      ru_current: null,
      ru_next: null,
      ky_current: null,
      ky_next: null
    }
  }
  componentDidMount() {
   this.connect();    
  }


  async connect(){
    const res     = await getToken();
    await window.localStorage.setItem('token', res.data.token); 

    const routes = await fetch(URL.routes, 'GET');
    this.setState({
      routes: routes.data.data,
    })
  }

  async handleRoute(e){
    e.preventDefault();
    const route = await fetch_by_id(URL.routes, e.target.value );
   await this.setState({
      ...this.state,
      is_platform: true,
      directions: route.data.directions,
      platforms: route.data.directions[0].platforms,
      selected: {
        ...this.state.selected,
        route: route.data,
        direction: route.data.directions[0],
        platform: route.data.directions[0].platforms[0]
      }    
    })
  }

  handleDirection = e => {
    e.preventDefault();
    const { directions } = this.state;
    const direction = directions.find(item => item.id === parseInt(e.target.value,10));

    this.setState({
      ...this.state,
      platforms: direction.platforms,
      selected: {
        ...this.state.selected,
        direction,
      }
    })
  }

 handlePlatform = (e) => {
    const { selected } = this.state;
    const platform = selected.direction.platforms.find(item => item.id === parseInt(e.target.value, 10))
    this.setState({
      ...this.state,
      is_platform: true,
      selected: {
        ...this.state.selected,
        platform: null,
      }
    }, ()=> this.update(platform))
    
  }

  update = (data) => {
    this.setState({
      ...this.state,
      is_platform: true,
      selected: {
        ...this.state.selected,
        platform: data,
      }
    })
  }

  handleUpload = e => {
    e.preventDefault();
    console.log(e.target.files[0].type)
    this.setState({
      ...this.state,
      is_correct: true,
      audio: {
        ...this.state.audio,
        [e.target.name]: e.target.files[0],
      }
    })
  }

  handleSubmit = () => {
    const { selected, audio, is_all } = this.state;
    if (selected.platform === null) {
      return
    }
    const platform = selected.platform.id;
    const direction = selected.direction.id
    

    const ruCurrData = new FormData();
    ruCurrData.append('lang', 'ru'); 
    ruCurrData.append('file', audio.ru_current); 
    ruCurrData.append('type', 'current');
    is_all && ruCurrData.append('direction', direction); 

    const ruNextData = new FormData();
    ruNextData.append('lang', 'ru'); 
    ruNextData.append('file', audio.ru_next); 
    ruNextData.append('type', 'current');
    is_all && ruNextData.append('direction', direction); 

    const kyCurrData = new FormData();
    kyCurrData.append('lang', 'ru'); 
    kyCurrData.append('file', audio.ky_current); 
    kyCurrData.append('type', 'current');
    is_all && kyCurrData.append('direction', direction); 

    const kyNextData = new FormData();
    kyNextData.append('lang', 'ru'); 
    kyNextData.append('file', audio.ky_next); 
    kyNextData.append('type', 'current');
    is_all && kyNextData.append('direction', direction); 

    audio.ru_current !== null && fetch(URL.platforms, 'POST', ruCurrData, platform, 'audios')
      .then(res => console.log(res))
      .catch(err => console.log(err));

      audio.ru_next !== null && fetch(URL.platforms, 'POST', ruNextData, platform, 'audios')
      .then(res => console.log(res))
      .catch(err => console.log(err));

      audio.ky_current !== null && fetch(URL.platforms, 'POST', kyCurrData, platform, 'audios')
      .then(res => console.log(res))
      .catch(err => console.log(err));

      audio.ky_next !== null && fetch(URL.platforms, 'POST', kyNextData, platform, 'audios')
      .then(res => console.log(res))
      .catch(err => console.log(err));
  }

  routeOpt = () => {
    const { routes } = this.state;
    return routes.map(item => <option key={item.id} value={item.id}>{item.name}-{item.type}</option>);
  }

  directionOpt = () => {
    const { directions } = this.state;
    return directions.map(item => <option key={item.id} value={item.id}>{item.type}</option>)
  }

  platformOpt = () => {
    const { platforms } = this.state;
    return platforms.map(item => <option key={item.id} value={item.id}>{item.station.name}</option>)
  }

  handleAll = (e) => {
    this.setState({
      ...this.state,
      is_all: e.target.checked
    })
  }

  render() {
    const { route, direction, platform, is_platform } = this.state.selected;
    let url_ruCurr = platform && 
                Object.keys(platform.audio).length !== 0 && 
                `http://transport.inobi.kg:5000/transport/audio/ru/${platform.audio.ru.current}`;
    let url_ruNext = platform && 
                Object.keys(platform.audio).length !== 0 && 
                `http://transport.inobi.kg:5000/transport/audio/ru/${platform.audio.ru.next}`;
    let url_kyCurr = platform && 
                Object.keys(platform.audio).length !== 0 && 
                `http://transport.inobi.kg:5000/transport/audio/ky/${platform.audio.ky.current}`;
    let url_kyNext = platform && 
                Object.keys(platform.audio).length !== 0 && 
                `http://transport.inobi.kg:5000/transport/audio/ky/${platform.audio.ky.next}`;
    return (
      <div>
        <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-around'}}>
          <select name="route" id="route" onChange={this.handleRoute.bind(this)}>
            {this.routeOpt()}
          </select>
          <select name="direction" id="direction" onChange={this.handleDirection}>
            {this.directionOpt()}
          </select>
          <select name="platforms" id="platforms" onChange={this.handlePlatform.bind(this)}>
            {this.platformOpt()}
          </select>
        </div>
        <div style={{margin: '20px'}}>
          <p>Route: {route && route.name}</p>
          <p>Direction: {direction && direction.type}</p>
          <p>Platform: {platform && platform.station.name}</p>
          <div><input type="checkbox" checked={this.state.is_all} label="For ALL?" onChange={this.handleAll.bind(this)} /></div>
        </div>
        <div>
          <div style={{margin: '10px', display: 'flex', flexDirection: 'row', justifyContent: 'space-around', width: 900}}>
              <p style={{marginRight: 50}}>RU: </p>
              <span>
                Current: 
                <input type="file" accept="audio/wav" name="ru_current" onChange={this.handleUpload} />
                {
                  platform && <audio controls>
                    <source src={url_ruCurr} type="audio/wav" />
                  </audio>
                }
              </span>
              <span>
                Next: 
                <input type="file" accept="audio/wav" name="ru_next" onChange={this.handleUpload} />
                {
                  platform && <audio controls>
                  <source src={url_ruNext} type="audio/wav" />
                </audio>
                }
              </span>
          </div>
          <div style={{margin: '10px', display: 'flex', flexDirection: 'row', justifyContent: 'space-around', width: 900}}>
              <p style={{marginRight: 50}}>KY: </p>
              <span>
                Current: <input type="file" accept="audio/wav" name="ky_current" onChange={this.handleUpload} />
                {
                  platform && <audio controls>
                  <source src={url_kyCurr} type="audio/wav" />
                  </audio>
                }
              </span>
              <span>
                Next: <input type="file" accept="audio/wav" name="ky_next" onChange={this.handleUpload} />
                {
                  platform && <audio controls>
                    <source src={url_kyNext} type="audio/wav" />
                  </audio>
                }
              </span>
          </div>
          <button onClick={this.handleSubmit}>Submit</button>
        </div>
      </div>
    );
  }
}

export default App;
