import axios from 'axios';


//TOKEN
export const getToken = () => {
  const loginURL = 'http://transport.inobi.kg:5000/app/v2/login';

  const auth = JSON.stringify({
    username: 'kurishov',
    pwd: 'cartrack',
  });

  return axios.post(loginURL, auth, {
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    },
  });
};

const jwt = 'Bearer '.concat(window.localStorage.getItem('token'));
const auth = {
  headers: {
    Authorization: jwt,
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  },
};

const config = {
  headers: {
    Authorization: jwt,
    'Content-Type': 'multipart/form',
  },
}

export const fetch = (url, method, data = {}, id, endpoint) => {
  switch (method) {
    case 'POST':
      if (endpoint) {
        return axios.post(url + `/${id}/${endpoint}`, data, config);
      } else {
        return axios.post(url, data, auth);
      }
    case 'PUT':
      return axios.put(url + `/${id}`, data, auth);
    case 'DELETE':
      return axios.delete(url + `/${id}`, auth);
    default:
      return axios.get(
        `${url}?${Object.keys(data)
          .map(k => `${k}=${data[k]}`)
          .join('&')}`,
        auth
      );
  }
};

export const fetch_by_id = (url, id) => {
  return axios.get(url + `/${id}`, auth);
};
